.bank-root {
  padding-top: 50px;
  height: calc(100vh - 50px); /*50 for padding*/
  position: relative;
}

.bank-header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bank-preivew-container {
  width: 600px;
  height: 200px;
  border: 2px solid var(--light-blue);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 6px -2px #272727;
  position: relative;
}

.bank-style-copy {
  margin-right: 20px;
  width: 350px;
  min-height: 200px;
  cursor: pointer;
  outline: none;
  border: none;
  background: transparent;
  padding: 10px;
  border: 2px solid var(--light-blue);
  box-shadow: 0px 0px 6px -2px #272727;
  transition: box-shadow 0.4s;
}

.bank-style-copy-copied {
  box-shadow: 0px 0px 8px -1px #272727;
}

.bank-copy-text {
  margin: 0px;
  padding: 0px;
  cursor: pointer;
  text-align: center;
  color: var(--light-blue);
  font-weight: 600;
}

.bank-bank {
  height: calc(50vh);
  width: 1000px;
  margin: 10px auto;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 5px 20px;
}

/* scroll-bar */
.bank-bank::-webkit-scrollbar {
  width: 10px;
}
.bank-bank::-webkit-scrollbar-thumb {
  background: var(--light-blue);
}

.bank-section-header {
  font-size: 2rem;
  color: var(--light-blue);
  border-bottom: 1px solid var(--light-blue);
  width: 100%;
}

.bank-custom-container {
  padding-left: 40px;
}

.bank-custom-header {
  font-size: 1.5rem;
  border-bottom: 1px solid #000;
  margin-bottom: 20px;
}

.bank-child-block {
  width: 20%;
  height: 20%;
  min-width: 20px;
  min-height: 20px;
  background: #000;
  margin: 3px;
  border-radius: 4px;
}

.bank-search-spacer {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.bank-search-container {
  display: flex;
}

.bank-search-input {
  width: 400px;
  padding: 5px;
  outline: none;
  border: none;
  background-color: var(--background-color);
  border-bottom: 2px solid var(--light-blue);
  margin-left: 5px;
  box-shadow: none;
  transition: box-shadow 0.3s;
  border-radius: 2px;
}

.bank-search-input:focus {
  box-shadow: 0px 4px 4px -4px #575757;
}
